import "./single-dream.css";
import React from "react";
import Header from "../../componentes/header/header";
import Footer from "../../componentes/footer/footer";
import Stars from "../../assets/stars.png";
import Wpp from "../../assets/wpp.png";
import Face from "../../assets/facebook.png";
import Twitter from "../../assets/twitter.png";
import Insta from "../../assets/insta.png";
import Link from "../../assets/link.png";
import DreamerIMG from "../../assets/sonhador.png";
import { useHistory, useRouteMatch } from "react-router-dom";
import Modal from "react-modal";
import Affirmations from "../../componentes/affirmations/affirmations";
import Quotas from "../../componentes/quotas/quotas";
import API from "../../utils/fetch";

export default function SingleDream() {
  const history = useHistory();
  const [modalIsOpen, setModalIsOpen] = React.useState(false);
  const [dadosSonhos, setDadosSonhos] = React.useState();
  const { params } = useRouteMatch();

  React.useEffect(() => {
    const getDream = async () => {
      const response = await API.dreamById(params.id);
      const data = response.data;
      setDadosSonhos(data.dados.dream);
    };
    getDream();
  }, []);

  const showModal = () => {
    setModalIsOpen(true);
  };

  const hideModal = () => {
    setModalIsOpen(false);
  };

  return dadosSonhos ? (
    <div className="SingleDream">
      <Header h1="" h2="" menu="true" arrow="true" />
      <div className="dreamer-img">
        <img
          src={dadosSonhos.picture ? dadosSonhos.picture : DreamerIMG}
          alt="foto de perfil"
        />
      </div>

      <div className="dream-content">
        <div className="dreamer">
          <h2>{dadosSonhos.name}</h2>
          <div className="data">
            <span>
              Cota: R$ {dadosSonhos.cashgoal / dadosSonhos.quotasquantity},00
            </span>
            <span>
              {dadosSonhos.risk == 0
                ? "Alto"
                : dadosSonhos.risk == 1
                ? "Médio"
                : "Baixo"}{" "}
              risco
            </span>
          </div>
          <hr></hr>
          <div className="data">
            <span>
              {dadosSonhos.quotasquantity - dadosSonhos.quotas}{" "}
              {dadosSonhos.quotasquantity - dadosSonhos.quotas <= 1
                ? " cota "
                : " cotas "}
              disponíveis
            </span>
            <span>
              {60 - dadosSonhos.expiration_date}{" "}
              {60 - dadosSonhos.expiration_date <= 1 ? "dia" : "dias"} restantes
            </span>
          </div>
          <span>Meta: {dadosSonhos.cashgoal},00 </span>

          <Quotas
            quotas={dadosSonhos.quotas}
            total={dadosSonhos.quotasquantity}
          />

          {dadosSonhos.quotas === 0 ? (
            <span className="first-time">
              <img src={Stars} alt="icone estrelas" />
              Seja o primeiro a apoiar {dadosSonhos.name}!
            </span>
          ) : (
            ""
          )}

          <Modal
            isOpen={modalIsOpen}
            className="ModalFirstTime"
            overlayClassName="OverlayFirstTime"
          >
            <Affirmations hideModal={hideModal} />
          </Modal>

          <a
            onClick={() => {
              if (dadosSonhos.quotas > 0) {
                showModal();
              }
              history.push(
                `${process.env.PUBLIC_URL}/apoiar-sonho/${params.id}`
              );
            }}
          >
            Apoiar este sonho
          </a>
        </div>
      </div>

      <div className="dream-data">
        <h4>Sobre o sonho de {dadosSonhos.name}</h4>
        <hr></hr>
        <div className="dream-description">
          <p>{dadosSonhos.description}</p>
          <div className="button">
            <a href="">Apoiar este sonho</a>
          </div>
        </div>
      </div>

      <div className="promo">
        Divulgar
        <hr></hr>
        <div className="social-media">
          <a href="">
            <img src={Wpp} alt="whatsap" />
          </a>
          <a href="https://www.facebook.com/Grana-Solidária-103550368137293/">
            <img src={Face} alt="facebook" />
          </a>
          {/* <img src={Twitter} alt="twitter" /> */}
          <a href="https://www.instagram.com/grana_solidaria/">
            <img src={Insta} alt="instagram" />
          </a>
          {/* <img src={Link} alt="link" /> */}
        </div>
      </div>
      <Footer />
    </div>
  ) : (
    <div></div>
  );
}
